body {
    background-color: #f8f9fa;
}

.bd-placeholder-img {
    font-size: 1.125rem;
    text-anchor: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

@media (min-width: 768px) {
    .bd-placeholder-img-lg {
        font-size: 3.5rem;
    }
}

html {
    font-size: 14px;
}

@media (min-width: 768px) {
    html {
        font-size: 16px;
    }
}

.container {
    max-width: 960px;
}

.pricing-header {
    max-width: 700px;
}

.card-deck .card {
    min-width: 220px;
}

.participante {
    width: 100%;
    height: 35px;
    transition: all 0.2s ease-in-out;
}

.participante:hover {
    transform: scale(1.9) !important;
    margin-left: 250px;
    border-color: #007bff;
    z-index: 3000;
    height: 50px;
}

.ficha-participante {
    height: 20px;
}

.dinero {
    font-size: 12px
}

.nomb-participante {
    font-size: 12px !important;
    font-weight: bold;
    white-space: nowrap;
    text-overflow: ellipsis;
    min-width: 30px!important;
}

.content-name-participante {
    height: 20px !important
}

.titulo-participantes {

    margin-bottom: 3px;
}

.list-group-item {
    padding: 0.0rem 1.25rem;
}

.numero-participante {
    font-size: 30px !important;
}

.activatePago {
    height: 35px;
    float: right;
    cursor: pointer;

}

.activadodinero {
    font-weight: bold;
    color: #007bff;
    font-size: 12px;
}

.nombre-participante {
    height: 18px;
}

.ganaste {
    background: #FFFF64;
}

.perdiste {
    background: red;
}

.mg-left {
    padding: 1rem !important;
    float: left;
}

#ruleta {
    float: left !important;
}

.img-modal{
    height: 250px !important;
}